:root {
    --primary: #fff;
}

.btn {
    padding: 8px 20px;
    border-radius: 2px;
    outline: none;
    border: none;
    cursor: pointer;
}

.btn--primary {
    background-color: var(--primary);
    color: #242424;
    border: 1px solid var(--primary);
}

.btn--outline {
    background-color: transparent;
    color: #fff;
    padding: 8px 12px;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-in-out;

}


.btn--medium {
    padding: 8px 20px;
    font-size: 20px;
    font-weight: 800;
    text-align: center ;
    justify-content: center;
    font-family: Montserrat,sans-serif;
}

.btn--large {
    padding: 12px 26px;
    font-size: 20px;
    font-family: Montserrat,sans-serif;
    font-weight: 600;
}

.btn--medium:hover, .btn--large:hover {
    background-color: #fff;
    color: #242424;
    transition: all 0.3s ease-in-out;
}
